.initial-page {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box-initial-page {
  width: 50%;
  height: 100vh;
  /* height: calc(100vh - 70px); */
}

.box-initial-page:nth-of-type(1) {
  /* width: 50%; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box-initial-page:nth-of-type(2) img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.box-initial-page__info {
  width: 60%;
  /* border: 1px solid red; */
}

.box-initial-page__logo {
  /* border: 1px solid red; */
  margin-bottom: 3.5rem;
}