@import url(https://fonts.googleapis.com/css2?family=Kaisei+Tokumin&family=Roboto:wght@100;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Urbanist:wght@400;500&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Urbanist", sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.7rem 
}

.container-general {
  width: 80%;
  margin: auto;
  margin-top: 2.7rem;
  /* border: 1px solid red; */
}

:root {
  /* --color-black-theme1: #261C2C; */
  --color-black-theme1: hsl(278, 22%, 14%);
  --color-black-theme1-light: hsla(278, 22%, 14%, 85%);
  --color-navy-theme1: #3E2C41;
  --color-blue-theme1: #5C527F;
  --color-blue-theme1-light: hsl(253, 22%, 41%);
  --color-night-theme1: #6E85B2;
  --color-teal-theme2: #D4ECDD;
  --color-navy-theme2: #345B63;
  --color-black-theme2: #152D35;
  --color-winter-theme2: #112031;

  --color-brown-theme3: #4A403A;
  --color-maroon-theme3: #A45D5D;
  --color-orange-theme3: #FFC069;
  --color-white-theme3: #EFEFEF;

  --color-white: #fff;
  --color-gray: #F5F6FA;
  --color-black: #000;
  --color-blue: #0070f3;

  --color-profile-name: #5C6C79;
  --color-text-default: #51525D;
}

/* *::selection {
  background: var(--color-black-theme1);
  color: var(--color-white);
} */

.input-control {
  width: 100%;
  font-size: 1.5rem;
  padding: 13px 11px;
  border: none;
  border-radius: 10px;
  margin-top: 1.3rem;
  margin-bottom: 1.3rem;
  box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px;
}

.input-control::-webkit-input-placeholder {
  font-size: 1.4rem;
  font-weight: 400;
}

.input-control:-ms-input-placeholder {
  font-size: 1.4rem;
  font-weight: 400;
}

.input-control::placeholder {
  font-size: 1.4rem;
  font-weight: 400;
}

.title-section-form {
  width: 50%;
}
.title-section-form h2{
  font-size: 2.4em;
  color: #fff;
  color: var(--color-white);
}
.initial-page {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box-initial-page {
  width: 50%;
  height: 100vh;
  /* height: calc(100vh - 70px); */
}

.box-initial-page:nth-of-type(1) {
  /* width: 50%; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box-initial-page:nth-of-type(2) img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.box-initial-page__info {
  width: 60%;
  /* border: 1px solid red; */
}

.box-initial-page__logo {
  /* border: 1px solid red; */
  margin-bottom: 3.5rem;
}
.btn-standard {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1.3rem;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  border: 2px solid var(--color-black-theme1);
  margin-top: 2rem;
  transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
  cursor: pointer;
}

.btn-form {
  background-color: var(--color-black-theme1);
  width: 100%;
  color: var(--color-white);
}

.btn-register {
  background-color: var(--color-black-theme1);
  color: var(--color-white);
  margin-left: 20px;
}

.btn-login {
  color: var(--color-black-theme1);
}

.btn-form:hover {
  background-color: var(--color-white);
  color: var(--color-black-theme1);
  box-shadow: -3px -3px 0px 0px var(--color-black-theme1);
}

.btn-register:hover {
  background-color: var(--color-white);
  color: var(--color-black-theme1);
  box-shadow: -3px -3px 0px 0px var(--color-black-theme1);
}

.btn-login:hover {
  background-color: var(--color-black-theme1);
  color: var(--color-white);
  box-shadow: -3px -3px 0px 0px var(--color-black-theme1);
}
.container-register {
  background-color: var(--color-gray);
  width: 100%;
  height: 100%;
  /* height: calc(100vh - 70px); */
  font-size: 1.8rem;

  display: flex;
  justify-content: center;
}

.section-register {
  width: 50%;
  height: 100%;
  display: inline-flex;
  padding-bottom: 2rem;
  /* border: 1px solid red; */
}

.section-register:nth-of-type(1) {
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-register:nth-of-type(2) {
  height: auto;
}

.section-register .section-register__info {
  width: 70%;
  height: 80%;
  padding-top: 2.3rem;
}

.section-register .section-register__info label {
  font-weight: 500;
  color: var(--color-black);
  margin-bottom: .6rem;
}

.section-register .section-register__logo {
  width: 40%;
  margin: auto;
  /* border: 1px solid red; */
}

.section-register:nth-of-type(2) {
  background-color: var(--color-black-theme1);
  display: flex;
  justify-content: center;
  align-items: center;
}


.knowledge-list {
  width: 100%;
  /* border: 1px solid red; */
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  gap: 10px;
}

.knowledge-list li {
  background-color: var(--color-white);
  width: 100%;
  padding: 10px 4px;
  list-style:none;
  font-weight: bold;
  font-size: 1.4rem;
  border: 2px solid var(--color-black-theme1);
  display: flex;
  justify-content: center;
  align-self: center;
  cursor: pointer;
}

.active {
  display: flex;
  background-color: var(--color-black-theme1) !important;
  color: var(--color-white);
}
.container-login {
  background-color: var(--color-gray);
  width: 100%;
  height: 100vh;
  /* height: calc(100vh - 70px); */
  font-size: 1.8rem;

  display: flex;
  justify-content: center;
}

.section-login {
  width: 50%;
  /* border: 1px solid red; */
}

.section-login:nth-of-type(1) {
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-login .section-login__info {
  width: 60%;
  height: 80%;
}

.section-login .section-login__info label {
  font-weight: 500;
  color: var(--color-black);
  margin-bottom: .6rem;
}

.section-login .section-login__logo {
  width: 40%;
  margin: auto;
  /* border: 1px solid red; */
}

.section-login:nth-of-type(2) {
  background-color: var(--color-black-theme1);
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}

.container-header a {
  text-decoration: none;
  color: var(--color-black);
}

.container-header__content {
  width: 80%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  border-bottom: 1px solid var(--color-text-default);
}

.container-header__content nav .logo {
  width: 27px;
}
.container-header__content img, svg {
  width: 100%;
}
.container-main-layouts {
  /* width: 80%;
  margin: auto; */
}
.container-cover-image {
  width: 100%;
  height: 150px;
  margin: auto;
  padding: 2rem;
  border-bottom: 1px solid var(--color-text-default);

  display: flex;
  justify-content: center;
  align-items: center;
}

.container-cover-image p {
  font-size: 2.6rem;
  font-style: italic;
}
.card-project {
  width: 100%;
  /* width: 33.3%; */
  
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.card-project__image {
  width: 100%;
}

.card-project__image img {
  width: 100%;
}

.card-project__body {
  padding: 15px 10px 25px 10px;
}

.card-project__technologies {
  width: 100%;
  display: flex;
}

.card-project__technologies .item-tech {
  margin-right: 10px;
}

.card-project-link {
  text-decoration: none;
  color: var(--color-black);
}
.container-feed {
  width: 80%;
  margin: auto;
}

.feed-projects {
  width: 100%;
  /* display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem; */
  padding-top: 2.6rem;
  padding-bottom: 4rem;
}
.container-profile {
  width: 100%;
  /* height: 100vh; */
}

.section-profile {
  padding-top: 4rem;
  padding-bottom: 1.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.profile-info {
  width: 50%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border: 1px solid blue; */
  margin-left: 25px; 
}

.profile-image {
  width: 180px;
  height: 180px;
  /* border: 1px solid green; */
}
.profile-image img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  object-fit: cover;
}


.box-info-principal {
  width: 90%;
  height: 100%;
}

.profile-name {
  color: #000;
  text-transform: capitalize;
  letter-spacing: -1px;
  font-size: 3.4rem;
  font-weight: 500;
} 

.profile-profession {
  color: var(--color-navy-theme1);
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: -.5px;
  font-size: 1.9rem;
  line-height: 10px;
  margin-bottom: 10px;
}

.profile-description {
  font-size: 1.5rem;
  margin-top: 10px;
  font-weight: lighter;
}

.profile-skills {
  width: 100%;
  padding-top: 15px;
  /* border: 1px solid green; */
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 6px;
  gap: 6px;
}

.profile-skill {
  background-color: hsla(278, 22%, 14%, 80%);
  color: var(--color-white);
  font-size: 1.3rem;
  text-align: center;
  padding: 8px 11px;
  border-radius: 25px;
}

.section-profile-info {
  width: 80%;
  margin: auto;
  padding-top: 3.4rem;
  padding-bottom: 2rem;
  border-top: 2px solid var(--color-gray);
  border-bottom: 2px solid var(--color-gray);
}
.container-single-project {
  width: 80%;
  margin: auto;
  /* display: grid;
  grid-template-columns: repeat(2, 1fr); */
  /* border: 1px solid red; */
  padding-bottom: 4rem;
}

.single-project__title {
  font-size: 3rem;
  margin-top: 2rem;
  margin-bottom: 1.4rem;
}

.single-project_technologies {
  display: flex;
  margin-bottom: 1.4rem;
  font-style: italic;
}

.single-project_technologies .item-tech {
  margin-right: 1rem;
}

.container-single-project img {
  width: 100%;
}
.container-form {
  padding-top: 2rem;
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: start;
  padding-bottom: 20px;
}

.container-form label {
  margin-top: 1.4rem;
  margin-bottom: 1.4rem;
}

.container-form__content {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-form__form {
  width: 80%;
  margin: auto;
  margin-top: 20px;
}

.container-form__form label {
  font-size: 1.7rem;
}

.container-trix {
  background-color: var(--color-white);
  width: 100%;
  font-size: 1.3rem;
  padding-left: 20px;
  margin-bottom: 10px;
}


.knowledge-list-addproject {
  width: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  gap: 10px;
}

.knowledge-list-addproject li {
  background-color: var(--color-white);
  width: 100%;
  padding: 11px 5px;
  list-style:none;
  font-size: 1.4rem;
  border: 2px solid var(--color-black-theme1);
  display: flex;
  justify-content: center;
  align-self: center;
  cursor: pointer;
}

.knowledge-list-addproject input {
  display: none;
}

